import React, { Component } from 'react';

class Footer extends Component {
    render() {
        return (
            <div className="col-xs-12 footer">
                <footer className="container-fluid text-center">
                    <hr />
                    <p style={{ color: '#00aef3' }}>  © E-Leaning, Inc. 2018</p>
                </footer>

            </div>
        );
    }
}

export default Footer;



